<h2>提示</h2>
<div class="tip">
<p><code>VUX</code> 是<code>库</code>而非<code>框架</code>，虽然有专用的 <code>vux-loader</code>，但并不影响你自由地使用其他组件库或者工具库。<br><br></p>
<p><code>VUX</code> 使用的 CSS 预处理工具是 <code>less</code>(同 WeUI)，但(利益于 .vue 单文件组件的灵活性)这并不影响你使用 <code>SASS</code> 等其他预处理器。<br><br></p>
<p>用以表示该组件库时请使用大写名字 <code>VUX</code>，用在说明版本号时使用小写 <code>vux@2.x</code>。</p>
